import { parseArgv, findOption } from './chromatic-test';

let origConsole;
beforeAll(() => {
  origConsole = global.console;
  global.console = {
    log: jest.fn(),
  };
});
afterAll(() => {
  global.console = origConsole;
});

jest.mock('../assets/environment', () => ({
  CHROMATIC_CREATE_TUNNEL: true,
  CHROMATIC_APP_CODE: 'env-code',
}));

jest.mock('jsonfile', () => ({
  readFileSync: () => ({
    scripts: {
      storybook: 'start-storybook -p 1337',
      otherStorybook: 'start-storybook -p 7070',
      notStorybook: 'lint',
      'build-storybook': 'build-storybook',
      otherBuildStorybook: 'build-storybook',
    },
  }),
}));

process.env.CHROMATIC_APP_CODE = 'test';

it('sets reasonable defaults', () => {
  expect(parseArgv(['node', 'chromatic-test', '--app-code', 'cli-code'])).toMatchObject({
    appCode: 'cli-code',
    buildScriptName: 'build-storybook',
    noStart: true,
    fromCI: false,
    autoAcceptChanges: undefined,
    exitZeroOnChanges: undefined,
    interactive: true,
    verbose: false,
    createTunnel: true,
    originalArgv: ['node', 'chromatic-test', '--app-code', 'cli-code'],
  });
});

it('picks up app-code from environment', () => {
  expect(parseArgv(['node', 'chromatic-test'])).toMatchObject({
    appCode: 'env-code',
  });
});

it('allows you to override defaults for boolean options', () => {
  expect(
    parseArgv([
      'node',
      'chromatic-test',
      '--ci',
      '--do-not-start',
      '--auto-accept-changes',
      '--exit-zero-on-changes',
      '--skip',
      '--debug',
      '--no-interactive',
    ])
  ).toMatchObject({
    noStart: true,
    skip: true,
    fromCI: true,
    autoAcceptChanges: true,
    exitZeroOnChanges: true,
    verbose: true,
    interactive: false,
    createTunnel: true,
  });
});

it('picks up default start script', () => {
  expect(parseArgv(['node', 'chromatic-test', '-s'])).toMatchObject({
    scriptName: 'storybook',
    url: 'http://localhost:1337/iframe.html',
    noStart: false,
  });
});

it('allows you to specify alternate build script', () => {
  expect(
    parseArgv(['node', 'chromatic-test', '--build-script-name', 'otherBuildStorybook'])
  ).toMatchObject({
    buildScriptName: 'otherBuildStorybook',
  });
});

it('allows you to specify alternate script, still picks up port', () => {
  expect(parseArgv(['node', 'chromatic-test', '--script-name', 'otherStorybook'])).toMatchObject({
    scriptName: 'otherStorybook',
    url: 'http://localhost:7070/iframe.html',
    noStart: false,
  });
});

it('allows you to specify alternate script, that does not start storybook, if you set port', () => {
  expect(
    parseArgv([
      'node',
      'chromatic-test',
      '--script-name',
      'notStorybook',
      '--storybook-port',
      '6060',
    ])
  ).toMatchObject({
    scriptName: 'notStorybook',
    url: 'http://localhost:6060/iframe.html',
  });
});

it('throws if you try to specify a script name that is not a storybook, if you do NOT set port', () => {
  expect(() => parseArgv(['node', 'chromatic-test', '--script-name', 'notStorybook'])).toThrow(
    /must pass a port/
  );
});

it('allows you to specify alternate command if you set port', () => {
  expect(
    parseArgv(['node', 'chromatic-test', '--exec', 'storybook-command', '--storybook-port', '6060'])
  ).toMatchObject({
    exec: 'storybook-command',
    url: 'http://localhost:6060/iframe.html',
  });
});

it('throws if you try to specify a command name, if you do NOT set port', () => {
  expect(() => parseArgv(['node', 'chromatic-test', '--exec', 'storybook-command'])).toThrow(
    /must pass a port/
  );
});

it('throws if you try to pass a script or command name and a url', () => {
  expect(() =>
    parseArgv([
      'node',
      'chromatic-test',
      '--exec',
      'storybook-command',
      '--storybook-url',
      'http://foo.bar',
    ])
  ).toThrow(/Can only use one of --exec, --storybook-url/);

  expect(() =>
    parseArgv([
      'node',
      'chromatic-test',
      '--script-name',
      'storybook',
      '--storybook-url',
      'http://foo.bar',
    ])
  ).toThrow(/Can only use one of --script-name, --storybook-url/);
});

it('throws if you try to pass a script or command name and a build script', () => {
  expect(() => parseArgv(['node', 'chromatic-test', '--exec', 'storybook-command', '-b'])).toThrow(
    /Can only use one of --build-script-name, --exec/
  );

  expect(() => parseArgv(['node', 'chromatic-test', '--script-name', 'storybook', '-b'])).toThrow(
    /Can only use one of --build-script-name, --script-name/
  );
});

it('throws if you try to pass a script or command name and a directory', () => {
  expect(() =>
    parseArgv([
      'node',
      'chromatic-test',
      '--exec',
      'storybook-command',
      '--storybook-build-dir',
      '/tmp/dir',
    ])
  ).toThrow(/Can only use one of --exec, --storybook-build-dir/);

  expect(() =>
    parseArgv([
      'node',
      'chromatic-test',
      '--script-name',
      'storybook',
      '--storybook-build-dir',
      '/tmp/dir',
    ])
  ).toThrow(/Can only use one of --script-name, --storybook-build-dir/);
});

it('throws if you try to pass a build script and a directory', () => {
  expect(() =>
    parseArgv(['node', 'chromatic-test', '-b', '--storybook-build-dir', '/tmp/dir'])
  ).toThrow(/Can only use one of --build-script-name, --storybook-build-dir/);
});

it('allows you to set a URL without path', () => {
  expect(
    parseArgv(['node', 'chromatic-test', '--storybook-url', 'https://google.com'])
  ).toMatchObject({
    noStart: true,
    url: 'https://google.com/iframe.html',
    createTunnel: false,
  });
});

it('allows you to set a URL with a path', () => {
  expect(
    parseArgv(['node', 'chromatic-test', '--storybook-url', 'https://google.com/foo'])
  ).toMatchObject({
    noStart: true,
    url: 'https://google.com/foo/iframe.html',
    createTunnel: false,
  });
});

it('allows you to set a URL with iframe.html already set', () => {
  expect(
    parseArgv([
      'node',
      'chromatic-test',
      '--storybook-url',
      'https://google.com/iframe.html?param=foo',
    ])
  ).toMatchObject({
    noStart: true,
    url: 'https://google.com/iframe.html?param=foo',
    createTunnel: false,
  });
});

describe('findOption', () => {
  it('handles short names', () => {
    const port = findOption('start-storybook -p 9001', '-p', '--port');
    expect(port).toBe('9001');
  });
  it('handles long names', () => {
    const port = findOption('start-storybook --port 9001', '-p', '--port');
    expect(port).toBe('9001');
  });
  it('handles equals', () => {
    const port = findOption('start-storybook --port=9001', '-p', '--port');
    expect(port).toBe('9001');
  });
  it('handles double space', () => {
    const port = findOption('start-storybook --port  9001', '-p', '--port');
    expect(port).toBe('9001');
  });

  it('handles complex scripts', () => {
    const port = findOption(
      "node verify-node-version.js && concurrently --raw --kill-others 'yarn relay --watch' 'start-storybook -s ./public -p 9001'",
      '-p',
      '--port'
    );
    expect(port).toBe('9001');
  });
});
